    gap: 2rem;
  }
}

.project-item {
  text-decoration: none;
  opacity: 0;
  transform: translateY(4rem);
  transition: cubic-bezier(0.4, 0, 0.2, 1) 800ms 200ms;
  flex: 0 1 calc(50% - 0.5rem);
}
.project-item:nth-child(5n-4) {
  flex: 0 0 100%;
}
@media (min-width: 670px) {
  .project-item {
    flex: 0 1 calc(50% - 1rem) !important;
  }
}
.project-item:hover img {
  transform: scale(1.01) !important;
}
.project-item.no-transition {
  transition: none;
}
.project-item.in-view, body.no-js .project-item {
  transform: translateY(0);
  opacity: 1;
}
.project-item:nth-child(2n) {
  transition-delay: 400ms;
}
.project-item:nth-child(4n) {
  transition-delay: 600ms;
}
.project-item:nth-child(4n-1) {
  transition-delay: 800ms;
}
.project-item--image {
  display: block;
  transition: transform cubic-bezier(0.4, 0, 0.2, 1) 400ms !important;
  border-radius: 0.2rem;
  border: 1px solid #f7f7f9;
}
.project-item--title {
  color: #818a91;
  font-size: 0.8333333333rem;
  font-weight: 500;
  line-height: 1.2;
}
.project-item--excerpt {
  font-weight: 500;
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  color: #373a3c;
  font-size: 0.8333333333rem;
  line-height: 1.2;
}

.project-image-mask {
  display: block;
  border-radius: 0.2rem;
  transition: all 0.5s;
  position: fixed;
  z-index: 9;
}

.nav {
  margin: 0;
  display: flex;
}
.nav__toggle {
  display: none;
}
.nav__toggle:checked ~ .nav {
  pointer-events: auto;
  transform: translateX(0);
}
.nav__toggle:checked ~ .nav .menu-item {
  transform: translateX(0);
}
.nav__toggle__icon {
  display: none;
  width: 2em;
  height: 3em;
  position: relative;
  transition: 0.5s ease-in-out;
  position: relative;
  z-index: 99;
}
@media screen and (orientation: portrait) {
  .nav__toggle__icon {
    display: block;
  }
}
.nav__toggle__icon span {
  display: block;
  position: absolute;
  height: 0.2rem;
  width: 100%;
  background: #3e23e9;
  border-radius: 15px;
  left: 0;
  transform: rotate(0deg);
  margin-top: -0.1rem;
  transition: 0.25s ease-in-out;
}
.nav__toggle__icon span:nth-child(1) {
  top: 30%;
}
.nav__toggle__icon span:nth-child(2), .nav__toggle__icon span:nth-child(3) {
  top: 50%;
}
.nav__toggle__icon span:nth-child(4) {
  top: 70%;
}
.nav__toggle:checked + .nav__toggle__icon {
  transform: rotate(90deg);
}
.nav__toggle:checked + .nav__toggle__icon span:nth-child(1), .nav__toggle:checked + .nav__toggle__icon span:nth-child(4) {
  top: 50%;
  width: 0%;
  left: 50%;
}
.nav__toggle:checked + .nav__toggle__icon span:nth-child(2) {
  transform: rotate(45deg);
}
.nav__toggle:checked + .nav__toggle__icon span:nth-child(3) {
  transform: rotate(-45deg);
}
@media screen and (orientation: portrait) {
  .nav {
    padding: 2rem;
    pointer-events: none;
    transform: translateX(-100%);
    position: fixed;
    z-index: 9;
    transition: transform 800ms cubic-bezier(0.4, 0, 0.2, 1);
    justify-content: flex-end;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
  }
  .nav .menu-item {
    transition: transform 800ms cubic-bezier(0.4, 0, 0.2, 1);
    transform: translateX(-500%);
  }
}
.nav .menu-item {
  font-weight: 500;
  display: block;
  padding: 0 1em;
  text-transform: lowercase;
  font-size: 0.8333333333rem;
}
@media screen and (orientation: portrait) {
  .nav .menu-item {
    font-size: 1.728rem;
    padding: 0.5em 0;
  }
}

